.main-left {
  width: 70%;
  padding: 5px 30px;
}
section {
  padding: 20px 10px;
  border-bottom: solid 2px rgba(0, 0, 0, 0.1);
}
section h1, section h2, section h3, section p, section a, section li, section img {
  padding: 10px 0;
}
section h1 {
  font-size: 30px;
  font-weight: bold;
}
section h2 {
  font-size: 28px;
  font-weight: bold;
}
section p {
  font-size: 18px;
}
section li {
  font-size: 20px;
}
section h3 {
  font-size: 20px;
}
section img {
  max-width: 80%;
}
section a {
  font-size: 20px;
  color: blue;
}
section a:hover {
  opacity: 0.8;
}
@media (max-width: 600px) {
  .main-left {
    width: 100%;
  }
}