.header-top {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  padding: 10px;
  height: 100px;
}
.header-top img {
  max-height: 100%;
  object-fit: contain;
}
.fas {
  display: none;
}
@media (max-width: 600px) {
  .header-top {
    height: 80px;
  }
  .fas {
    display: inherit;
    position: absolute;
    top: 40px;
    right: 20px;
    transform: translateY(-50%);
    font-size: 25px;
  }
}