.header-nav {
  margin-top: 100px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  transition-duration: 0.3s;
}
.nav-item {
  width: 13%;
}
.nav-item.current {
  background: rgb(240, 199, 142);
}
.nav-item.current:hover {
  background: rgb(240, 199, 142);
  opacity: 0.9;
}
.nav-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
.nav-item a {
  text-decoration: none;
  text-align: center;
}
.nav-item h1 {
  padding-bottom: 5px;
  padding-top: 3px;
}
.nav-item img {
  width: 40px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 600px) {
  .header-nav {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition-duration: 0.3s;
    flex-direction: column;
    justify-content: space-between;
    height: calc(100vh - 300px);
  }
  .nav-item {
    width: 100%;
    padding: 17px 0;
    border-radius: 4px;
  }
  .nav-item img {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    vertical-align: middle;
  }
  .nav-item h1 {
    display: inline;
    vertical-align: middle;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: bold;
  }
  .header-nav.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}