.footer-images {
  position: fixed;
  left: 0;
  bottom: 50px;
  transform: translateY(50px);
  width: 100%;
  height: 20vh;
  transition-duration: 0.7s;
}
.footer-images.active {
  transform: translateY(0);
}
.footer-image {
  position: absolute;
  left: 0;
  transform: translateY(20vh);
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.7s;
  opacity: 0;
  visibility: hidden;
}
.footer-image.active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
@media (max-width: 600px) {
  .footer-images {
    height: 15vh;
  }
  .footer-images.active {
    transform: translateY(50px);
  }
}